'use client';

import { Auth0Provider } from '@auth0/auth0-react';
import ThemeProvider from "@/components/theme-provider";
import { Header } from "@/components/header";
import { Sidebar } from "@/components/sidebar";
import RequireAuth from "@/components/RequireAuth";
import "@/app/globals.css";

// Individual debug logs for each environment variable
console.log("NEXT_PUBLIC_AUTH0_DOMAIN:", process.env.NEXT_PUBLIC_AUTH0_DOMAIN);
console.log("NEXT_PUBLIC_AUTH0_CLIENT_ID:", process.env.NEXT_PUBLIC_AUTH0_CLIENT_ID);
console.log("NEXT_PUBLIC_AUTH0_AUDIENCE:", process.env.NEXT_PUBLIC_AUTH0_AUDIENCE);
console.log("NEXT_PUBLIC_REDIRECT_URI:", process.env.NEXT_PUBLIC_REDIRECT_URI);

// Get environment variables with fallbacks
const domain = process.env.NEXT_PUBLIC_AUTH0_DOMAIN || 'MISSING_DOMAIN';
const clientId = process.env.NEXT_PUBLIC_AUTH0_CLIENT_ID || 'MISSING_CLIENT_ID';
const audience = process.env.NEXT_PUBLIC_AUTH0_AUDIENCE || 'MISSING_AUDIENCE';
const redirectUri = process.env.NEXT_PUBLIC_REDIRECT_URI || 'MISSING_REDIRECT_URI';

console.log("Auth0Provider config:", {
  domain: process.env.NEXT_PUBLIC_AUTH0_DOMAIN,
  clientId: process.env.NEXT_PUBLIC_AUTH0_CLIENT_ID,
  audience: process.env.NEXT_PUBLIC_AUTH0_AUDIENCE,
  redirectUri: process.env.NEXT_PUBLIC_REDIRECT_URI,
});

// Additional debug logging for the variables we'll use
console.log({
  domain,
  clientId,
  audience,
  redirectUri,
});

if (
  domain === 'MISSING_DOMAIN' ||
  clientId === 'MISSING_CLIENT_ID' ||
  audience === 'MISSING_AUDIENCE' ||
  redirectUri === 'MISSING_REDIRECT_URI'
) {
  console.error('One or more Auth0 environment variables are missing!');
}

export default function RootLayout({ children }: { children: React.ReactNode }) {
  return (
    <html lang="en">
      <body>
        <Auth0Provider
          domain={domain}
          clientId={clientId}
          authorizationParams={{
            redirect_uri: redirectUri,
            audience: audience,
          }}
        >
          <RequireAuth>
            <ThemeProvider>
              <div className="min-h-screen flex">
                <Sidebar />
                <div className="flex-1">
                  <Header />
                  <main className="app-container">{children}</main>
                </div>
              </div>
            </ThemeProvider>
          </RequireAuth>
        </Auth0Provider>
      </body>
    </html>
  );
}
